<template>
<div>
    <!--农场-->
    <div class="panel panel-info">
        <div class="panel-body">
            <div>实时监测</div>
            <div>
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <div>
                            <h4>
                                <span v-for="(item,index) in typeCheckData" :key="index" v-html="item.name+'&nbsp;&nbsp;&nbsp;&nbsp;'" v-show="index<typenum||sensorInfVisible" :class="['iconfont',{'icon-farm':index==0,'icon-greenHouse':index==1,'icon-device':index==2,'icon-sensor':index==3,}]"></span>
                                <span v-show="!sensorInfVisible" v-html="initData[typenum].title" :class="['iconfont',{'icon-farm':typenum==0,'icon-greenHouse':typenum==1,'icon-device':typenum==2,'icon-sensor':typenum==3,}]"></span>
                                <a v-if="typenum!=0" class="btn btn-link pull-right" @click="loginItems(-1)">返回上一级</a>
                            </h4>
                        </div>
                    </div>
                    <div class="panel-body">
                        <!--选择位置-->
                        <div v-show="!sensorInfVisible">
                            <div v-if="items.length>0">
                                <div class="col-sm-3 col-md-3 col-lg-2" v-for="(item,index) in items" :key="index">
                                    <div class="item-back" @click="loginItems(1,item)">
                                        <div class="real-item">
                                            <div class="real-icon">
                                                <i class="iconfont " :class="['iconfont',{'icon-farm':typenum==0,'icon-greenHouse':typenum==1,'icon-device':typenum==2,'icon-sensor':typenum==3,}]"></i>
                                            </div>
                                            <div class="real-info">
                                                <span v-html="item.name"></span><br>
                                                <span v-html="item.address"></span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div v-show="items.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error">
                                未查找到数据，点击请重试
                            </div>
                        </div>
                        <!--传感器详细信息-->
                        <div v-show="sensorInfVisible">
                            <div class="pull-right">
                                <a class="btn btn-link"  @click="watchDraw(0)" v-show="NowAndHistory!=0">查看当前数据</a>
                                <a class="btn btn-link"  @click="watchDraw(1)" v-show="NowAndHistory!=1">查看历史记录</a>
                                <a class="btn btn-link"  @click="watchDraw(2)" v-show="NowAndHistory!=2">查看数据预测</a>
                            </div>
                            <div class="sensorHis">
                                <div v-if="NowAndHistory==1||NowAndHistory==2">
                                    <div class="sensorhis-header">
                                        <span v-show="NowAndHistory==1"> 选择历史记录时间段</span>
                                        <span v-show="NowAndHistory==2"> 选择专家预测方式</span>
                                    </div>
                                    <div class="sensorhis-body">
                                        <ul>
                                            <li v-for="(item,index) in sensorHis.item[NowAndHistory-1]" :key="index" :class='[{"item-active":sensorHis.now==index},"item-link"]' @click="changeHisType(index)">{{item.title}}</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                            <!--当前信息-->
                            <div v-show="NowAndHistory==0">
                                <div id="myChartNow" :style="{width: '600px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!--历史记录-->
                            <div v-show="NowAndHistory==1||NowAndHistory==2">
                                <div id="myChartHistory" :style="{width: '800px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!-- <div v-show="NowAndHistory==2">
                                <div id="myChartDataPrediction" :style="{width: '800px', height: '500px',margin:'auto'}"></div>
                            </div> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import chartFun from "@/echarts/index";
export default {
  data() {
    return {
      sensorInfVisible: false, //是否显示传感器图表数据
      NowAndHistory: 0, //当前数据，历史记录，数据预测切换
      typenum: 0, //当前所在type
      oldtypenum: 0, //上一级type
      restypenum: 0, //下一级type
      items: [],
      sensorInf: {
        max: 35, // 当前节点种植作物环境上限
        min: 26, // 当前节点种植作物环境下限
        now: 24, // 当前节点当前温度
        type: "温度|气压" //传感器类型
      },
      typeCheckData: [],
      //农场及以下信息初始化
      initData: [
        {
          url: "Sensor/selectAddress.action",
          title: "农场"
        },
        {
          url: "Sensor/selectGreenHouse.action",
          title: "大棚"
        },
        {
          url: "Sensor/selectGreenHouseEquipment.action",
          title: "设备"
        },
        {
          url: "Sensor/selectSensor.action",
          title: "传感器"
        }
      ],
      //传感器查询数据可选择列表，历史记录时间和数据预测方式选择
      sensorHis: {
        item: [
          [
            {
              title: "15分钟内历史记录数据",
              url: "collect/selectQuarter.action"
            },
            {
              title: "1小时内历史记录数据",
              url: "collect/selectQuarter.action"
            },
            {
              title: "一天内历史记录数据",
              url: "collect/selectQuarter.action"
            }
          ],
          [
            {
              title: "最小二乘法预测",
              url: "predict/predictOneDay.action"
            },
            {
              title: "灰色预测",
              url: "predict/predictOneDayGM.action"
            }
          ]
        ],
        now: 0
      }
    };
  },
  mounted() {
    this.loginThisItems(this.initData[this.typenum].url);
  },
  //vue-socket.io定义
  sockets: {
    // sensorMaxAndMin(obj) {
    //     console.log(obj)
    //   vm.sensorInf=obj;
    // },
    // sensorValue(obj){
    //      console.log(obj)
    //    vm.sensorInf.now=obj;
    // }
  },
  methods: {
    //判断层次改变
    loginItems(direction, item = "") {
      var vm = this;
      var typenum = vm.typenum;
      if (item != "") {
        vm.typeCheckData[typenum] = item;
      }
      if (typenum == vm.initData.length - 1) {
        if (direction > 0) {
          vm.NowAndHistory = 0;
          vm.drawChartNow();
        } else if (direction < 0 && vm.sensorInfVisible) {
          vm.sensorInfVisible = false;
          vm.NowAndHistory = true;
          return;
        }
      }
      vm.oldtypenum = typenum;
      if (direction > 0) {
        typenum += 1;
      } else {
        typenum -= 1;
      }
      vm.restypenum = typenum;
      if (typenum <= vm.initData.length - 1) {
        vm.loginThisItems(
          vm.initData[typenum].url,
          item != ""
            ? item.id
            : typenum == 0
              ? ""
              : vm.typeCheckData[typenum].id
        );
      } else {
        vm.typenum = vm.initData.length - 1;
      }
    },
    //层次改变的请求
    loginThisItems(url, id = "") {
      var vm = this;
      vm.$axios
        .post(
          vm.HOST + url,
          id != ""
            ? {
                id: id
              }
            : {
                id: "2"
              }
        )
        .then(success => {
          if (success.data.length > 0) {
            vm.items = success.data;
            vm.typenum = vm.restypenum;
          } else {
            vm.typenum = vm.oldtypenum;
          }
        })
        .catch(error => {});
    },
    //监听需要绘制的界面
    watchDraw(index) {
      var vm = this;
      vm.sensorHis.now = 0;
      vm.NowAndHistory = index;
      if (index == 0) {
        vm.drawChartNow();
      } else {
        vm.$socket.emit("outGetValue",false);
        vm.drawChartHistory();
      }
    },
    //绘制当前信息
    drawChartNow() {
      var vm = this;
      let myChart = vm.$echarts.init(document.getElementById("myChartNow"));
      var splitNumber = 10;
      var unittype = vm.drawChartSensorType(0);
      var option = {};
      var min = vm.sensorInf.min;
      var max = vm.sensorInf.max;
      var len = max - min;
      if (len > 10 || len < 6) {
        if (len > 10 && len < 20) {
          splitNumber = 10;
        }
      } else if (len <= 10 && len >= 6) {
        splitNumber = len + 4;
      }
      option = chartFun.sensorInfNow(unittype, splitNumber, min, max, len);
      myChart.setOption(option, true);
      vm.sensorInfVisible = true;
       vm.$socket.on("onGetValue",(obj)=>{
        vm.sensorInf=obj;
        ResSensorInf(unittype.name);
      })
      function ResSensorInf(title) {
        var sensorInf=vm.sensorInf;
            option.series[0].data[0].value = sensorInf.now;
            var bool1 = sensorInf.now >= sensorInf.max;
            var bool2 = sensorInf.now <= sensorInf.min;
            var ns = title;
            if (bool1) {
              ns += "过高";
            } else if (bool2) {
              ns += "过低";
            }
            if(bool1||bool2){
              vm.$layer.alert(ns, {
              icon: 5,
              time: 1500 //1s后自动关闭
            });
            }
            myChart.setOption(option, true);
          }
    },
    //绘制历史记录
    drawChartHistory() {
      var vm = this;
      var sensorHis = vm.sensorHis;
      var NowAndHistory = vm.NowAndHistory - 1;
      var item = sensorHis.item[NowAndHistory][sensorHis.now];
      let myChart = vm.$echarts.init(document.getElementById("myChartHistory"));
      var unittype = vm.drawChartSensorType(0);
      vm.$axios
        .post(vm.HOST + item.url, {
          sensor_id: vm.typeCheckData[vm.typenum].id
        })
        .then(success => {
          var resdata = success.data.list;
          var xdata = [];
          var data = [];
          resdata.forEach(item => {
            xdata.push(vm.$api.opreate.DateCompletion(item.collectTime));
            data.push(item.sensorValue);
          });
          var option = chartFun.sensorInfHis(
            unittype,
            xdata,
            data,
            {
              show: true,
              start: 70
            },
            item.title + "曲线图"
          );
          myChart.setOption(option, true);
        })
        .catch(error => {});
    },

    //传感器类型对应单位和名称
    drawChartSensorType(i) {
      var data = [
        {
          unit: "℃",
          name: "温度"
        },
        {
          unit: "Pa",
          name: "气压"
        },
        {
          unit: " %RH",
          name: "湿度"
        }
      ];
      return data[i];
    },
    //改变历史记录和数据预测
    changeHisType(index) {
      var vm = this;
      vm.sensorHis.now = index;
      vm.drawChartHistory();
    }
  }
};
</script>

<style scoped>
.logindata-error {
  font-size: 18px;
  color: rgb(243, 29, 29);
}

.item-back {
  border: #ccc solid 1px;
  position: relative;
}

.item-back:hover {
  cursor: pointer;
}

.item-back:active {
  transform: scale(0.97);
}

.item-back .real-item {
  display: flex;
  align-items: center;
}

.item-back .real-item .real-icon {
  margin: 0 10px;
}

.sensorHis {
  float: left;
  display: flex;
  height: 500px;
  align-items: center;
  position: relative;
  left: 100px;
  width: 180px;
  flex-wrap: wrap;
}

.sensorHis .sensorhis-header {
  position: relative;
  top: -30px;
  font-weight: 600;
  font-size: 16px;
  font-family: "黑体";
  text-align: center;
}

.sensorHis .sensorhis-body ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}
.sensorhis-body .item-link {
  color: #1675e0;
  cursor: pointer;
  padding: 0.5rem;
}
.sensorhis-body .item-active {
  color: #f70b0b;
}
</style>



